const TASK_STATE = ["TODO", "", ""];
const TASK_PRIORITY = ["", "", ""];

export const outputLog = (
  type: string,
  oldState: string | number,
  newState: string | number
) => {
  switch (type) {
    case "create":
      return `[]${newState}`;
    case "name":
      return `[]${
        oldState ? `${oldState}` : ""
      }${newState}`;
    case "description":
      return `[]${
        oldState ? `${oldState}` : ""
      }${newState}`;
    case "user":
      return "";
    case "startDateAt":
      return `[]${
        oldState ? `${oldState}` : ""
      }${newState}`;
    case "endDateAt":
      return `[]${
        oldState ? `${oldState}` : ""
      }${newState}`;
    case "state":
      return `[]${TASK_STATE[Number(oldState)]}${
        TASK_STATE[Number(newState)]
      }`;
    case "progress":
      return `[]${
        oldState ? `${oldState}` : ""
      }${newState}`;
    case "priority":
      return `[]${TASK_PRIORITY[Number(oldState)]}${
        TASK_PRIORITY[Number(newState)]
      }`;
    case "todaysTask":
      return `[]${
        newState === "true"
          ? ""
          : ""
      }`;
    case "project":
      return ``;
  }
};
